/**
 * @module ui-bb-navigate-to-ng
 * @description
 * Create navigation link to specified page
 *
 * @example
 * // In an extension:
 * // file: scripts/index.js
 * import uiBbNavigateToNg from 'ui-bb-navigate-to-ng';
 *
 * export const dependencyKeys = [
 *   uiBbNavigateToNg,
 * ];
 *
 * // file: templates/template.ng.html
 * <ui-bb-navigate-to-ng
 *   data-action="'notifications'"
 *   data-label="'label.allNotifications' | i18n">
 * </ui-bb-navigate-to-ng>
 */
import angular from 'vendor-bb-angular';
import intentModuleKey, { bbIntentKey } from 'lib-bb-intent-ng';

import component from './component';
import controller from './controller';

export default angular
  .module('ui-bb-navigate-to-ng', [intentModuleKey])
  .component('uiBbNavigateToNg', component)
  .controller('controller', [bbIntentKey, controller])
  .name;



