/**
 * @name uiBbNavigateController
 * @type {function}
 *
 * @description
 * Navigate to widget controller
 */
export default function controller(bbIntents) {
  const ctrl = this;

  /**
   * @name navigateTo
   * @type {function}
   *
   * @description
   * Navigate to selected widget
   */
  const navigateTo = () => {
    if (ctrl.action) {
      const intent = bbIntents.create(ctrl.action);

      if (intent) {
        intent();
      }
    }
  };

  Object.assign(ctrl, {
    navigateTo,
  });

  return ctrl;
}



